/**
 * =============================================================================
 * Modules - Chapter 4 Profile cards
 * =============================================================================
 */

.vp-card {
    position: relative;
    width: 100%;
    height:520px;
    border: 6px solid map-get($colors, blue-lighter );


    @media screen and ( min-width:500px ) and ( max-width:750px ) {
        width: 75%;
    }

    @include media-breakpoint-up( md ) {
        height:595px;
        width: 388px;
    }
}

.vp-card-title {
    p {
        font-size: 24px;
        line-height: 1.1;
    }
}

.vp-card-image {
    // border-radius: 15px;
    position: relative;
    height: 175px;

    svg {
       max-width: 100%;
       max-height: 100%;
    }
}

.vp-card-age {
    font-size: 18px;
    color: map-get($colors, blue-lighter );
    font-weight: 600;
    @include media-breakpoint-up( md ) {
        font-size: 22px;
    }
}

.vp-text-strike {
    text-decoration: line-through;
}

.vp-card-age-flip {
    font-size: 18px;

    span {
        font-size: 18px;
    }

    @include media-breakpoint-up( md ) {
        font-size: 26px;

        span {
            font-size: 26px;
        }
    }

    .card-cross-age {
        font-size: 18px;

        @include media-breakpoint-up( md ) {
            font-size: 26px;
        }

        position: relative;

        &:before {
            position: absolute;
            right: -10%;
            bottom: 10%;
            content: '';
            height: 50%;
            width: 120%;
            border-top: 5px solid;
            border-color: map-get($colors, orange );
            transform: rotate(15deg);
        }
        &:after {
            position: absolute;
            left: -10%;
            bottom: 10%;
            content: '';
            height: 50%;
            width: 120%;
            border-top: 5px solid;
            border-color: map-get($colors, orange );
            transform: rotate(-15deg);
        }
    }

    .card-real-age {
        font-size: 18px;

        @include media-breakpoint-up( md ) {
            font-size: 26px;
        }

        color: map-get($colors, orange );
    }
}

.vp-card-likes {
    font-size: 18px;
    color: map-get($colors, blue-lighter );

    @include media-breakpoint-up( md ) {
        font-size: 22px;

        span {
            font-size: 22px;
        }
    }
}

.vp-card-likes-flip {
    font-size: 18px;
    color: map-get($colors, orange );

    span {
        font-size: 18px;
    }

    @include media-breakpoint-up( md ) {
        font-size: 22px;

        span {
            font-size: 22px;
        }
    }
}

.vp-card-buttons {
    position: absolute;
    bottom: 0;
    left: 0;
}

.vp-card-btn {
    cursor: pointer;
    background: map-get($colors, white );
    border: none;
    border-radius: 20px;
    overflow: hidden;
    font-size: 22px;
    font-weight: 500;
    box-shadow: 5px 5px 0 2px map-get($colors , blue-lightest );
    transition: box-shadow .3s $easeOutSine;

    @include media-breakpoint-up( md ) {
        font-size: 26px;
    }
    &:hover,
    &:active,
    &:focus {
        outline: none;
    }

    &:hover {
        transition: box-shadow .3s $easeOutSine;
        box-shadow: 0 0 0 2px map-get($colors , blue-lightest );
    }

    &.vp-card-like {
        transition: box-shadow .3s $easeOutSine;
        color: map-get($colors, black-lightest );
        padding: 12px 31px;

        @include media-breakpoint-up( md ) {
            padding: 22px 62px;
        }
    }

    &.vp-card-reverse {
        transition: box-shadow .3s $easeOutSine;
        padding: 12px 14px;

        @include media-breakpoint-up( md ) {
            padding: 22px 24px;
        }
        svg {
            margin-right: 6px;
            path {
                stroke: map-get($colors, black-lightest );
            }
        }
    }

    &.vp-card-info {
        transition: box-shadow .3s $easeOutSine;
        color: map-get($colors, orange );
        padding: 12px 14px;

        @include media-breakpoint-up( md ) {
            padding: 22px 24px;

        }
    }

}

.vp-card-default {
    opacity: 0;

    & * {
        opacity: 0;
    }

    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateY(180deg);
    transition: transform .4s $easeInOutSine;
}

.vp-card-flip {
    opacity: 0;

    & * {
        opacity: 0;
    }

    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateY(180deg);
    transition: transform .4s $easeInOutSine;
}

.vp-card-back {
    opacity: 0;

    & * {
        opacity: 0;
    }

    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateY(180deg);
    transition: transform .4s $easeInOutSine;

    ul {
        list-style: none;
    }

    p {
        font-size: 18px;
    }

    button{
        cursor: pointer;
        transition: all .3s $easeOutSine;
        display: block;
        position: absolute;
        background:transparent;
        top: 5px;
        right: 5px;
        border: none;
        width: 48px;
        height: 48px;
        padding: 0;
        outline: none;

        span {
            display: block;
            position: absolute;
            right: 2px;
            top: 4px;
            width: 40px;
            height: 40px;

            &:after {
                position: absolute;
                left: 15px;
                content: '';
                height: 40px;
                width: 5px;
                border-radius: 15px;
                background-color: map-get($colors, blue-lighter );
                transform: rotate(-45deg);
            }

            &:before {
                position: absolute;
                left: 15px;
                content: '';
                height: 40px;
                width: 5px;
                border-radius: 15px;
                background-color: map-get($colors, blue-lighter );
                transform: rotate(45deg);
            }
        }
        &:hover {
            transition: all .3s $easeOutSine;

            transform: rotate(90deg);
            span {
                transform: scale(1.1);
                transition: 0.3s linear ease-in-out
            }
        }
    }
}

.vp-card-active {
    opacity: 1;
    visibility: visible;
    position: static;
    transform: rotateY(0deg);
    transition: transform .6s $easeInOutSine;

    & * {
        opacity: 1;
        transition: opacity .8s $easeInOutCubic;
    }
}

.swiper-slide {
    perspective: 1000px;
    transform-style: preserve-3d;
    transition: all 5s $easeOutBack;
}

.vp-swiper-pagination {
    display: flex;

    @include media-breakpoint-up ( md ) {
        display: none;
    }
}

.swiper-button-next {
    display: none;
    outline: none;
    &.swiper-button-disabled {
        display: none;
    }

    @include media-breakpoint-up( md ) {
        display: block;
        background: none !important;
        height: 76px !important;
        width: 68px !important;
        margin-top: -76px !important;

        svg {
            height: 100%;
            transform: rotate(-90deg);

            path {
                stroke: map-get($colors, white );
            }
        }

        &:hover {
            cursor: pointer;

            svg {
                path {
                    transition: all .3s $easeOutSine;
                    fill: map-get($colors, white );
                }
            }
        }
    }
}

.swiper-button-prev {
    display: none;
    outline: none;

    &.swiper-button-disabled {
        display: none;
    }

    @include media-breakpoint-up( md ) {
        display: block;
        background: none !important;
        height: 76px !important;
        width: 68px !important;
        margin-top: -76px !important;

        svg {
            height: 100%;
            transform: rotate(90deg);

            path {
                stroke: map-get($colors, white );
            }
        }

        &:hover {
            cursor: pointer;

            svg {
                path {
                    fill: map-get($colors, white );
                }
            }
        }
    }
}


