

@keyframes distortion {
    $steps: 20;
    @for $i from 0 through $steps {
        #{percentage($i*(1/$steps))}{
            clip:rect(random(206)+px, 9999px, random(206px)+px,0);
        }
    }
}

@keyframes distortion-2 {
    $steps: 30;
    @for $i from 0 through $steps {
        #{percentage($i*(1/$steps))}{
            clip:rect(random(206)+px, 9999px, random(206px)+px,0);
        }
    }
}


.distortion-item {
    color: map-get($colors, grey);
    font-size: 170px;
    position: relative;
    width: 800px;
    margin:0 auto;
}

.vp-inaccurate-glitch {
    .distortion-item-before {
        position: absolute;
        left: -5px;
        text-shadow: -5px 0 map-get($colors, blue);
        top: 0;
        color: map-get($colors, blue-lightest);
        background: map-get($colors, blue-lightest);
        overflow: hidden;
        clip: rect(0,900px,0,0);
        animation: distortion 2s infinite linear alternate-reverse;
    }

}

.vp-inaccurate-glitch {
    .distortion-item-after {
        position: absolute;
        left: 5px;
        text-shadow: -4px 0 map-get($colors, blue);
        top: 0;
        color: map-get($colors, blue-lightest);
        background: map-get($colors, blue-lightest);
        overflow: hidden;
        clip: rect(0,900px,0,0);
        animation: distortion-2 3s infinite linear alternate-reverse;
    }
}



