/**
 * =============================================================================
 * Modules - Navigation
 * =============================================================================
 */


nav {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
}

.vp-navigation {
    opacity: 0;
    visibility: hidden;
    transition: all 0.8s $easeOutExpo;
    transform: translateY(-100%);
    position: fixed;
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
    &::-webkit-scrollbar { width: 0 !important }

    &.vp-menu-active {
        transform: translateY(0) ;
        opacity: 1;

        a {
            &:hover {
                div {
                    transform: translateY(-2%) scale(1.1);
                    transition: all 0.2s $easeOutBack;
                    transition-delay: 0s;
                }

                svg {
                    transform: scale(1.2);
                }
            }
        }

        .nav-intro {
            opacity: 1;
            transition-delay: .6s;
        }

        .nav-chap-1 {
            opacity: 1;
            transition-delay: .4s;
            transform: translateY(0);
        }

        .nav-chap-2 {
            transform: translateY(0);
            opacity: 1;
            transition-delay: .45s;
        }

        .nav-chap-3 {
            transform: translateY(0);
            opacity: 1;
            transition-delay: .5s;
        }

        .nav-chap-4 {
            transform: translateY(0);
            opacity: 1;
            transition-delay: .55s;
        }

        .nav-chap-5 {
            transform: translateY(0);
            opacity: 1;
            transition-delay: .6s;
        }

        .vp-nav-share li:nth-of-type(1) {
            transform: translateY(0);
            opacity: 1;
            transition-delay: .8s;
        }
        .vp-nav-share li:nth-of-type(2) {
            transform: translateY(0);
            opacity: 1;
            transition-delay: 1s;
        }
    }

    a {
        display: block;
        position: relative;
        z-index: 0;
        text-decoration: none;
    }
    .vp-nav-share li:nth-of-type(1) {
        z-index: 0;
        opacity: 0;
        transform: translateY(100%);
        transition: all 0.3s $easeOutExpo;
    }

    .vp-nav-share li:nth-of-type(2) {
        z-index: 0;
        opacity: 0;
        transform: translateY(100%);
        transition: all 0.3s $easeOutExpo;
    }

    .nav-chap {
        display: block;
        position: relative;
        z-index: 0;
        text-decoration: none;
        opacity: 0;
        transform: translateY(100%);
        transition: all 0.3s $easeOutExpo;
    }

    .nav-home-cta {
        opacity: 0;
        transform: translateY(100%);
        transition: all 0.3s $easeOutExpo;
    }

    .nav-icons {
        position: absolute;
        right:0;
        top: 0;
        z-index: -1;
        height: 100%;
        width: 100%;

        svg {
            position: absolute;
            right:0;
            top: 20%;
            height: 100%;

            @include media-breakpoint-up( md ) {
                right:15%;
            }

            g {
                width: 100%;
                path {
                    width: 100%;
                }
            }
        }
    }

    .nav-intro {
        font-size: $font-size-h2;
        line-height: $line-height-h2;
        opacity: 0;
        transition: all 0.3s $easeInOutBack;

        @include media-breakpoint-up( md ) {
            font-size: $font-size-h3-md;
            line-height: $line-height-h3-md;
        }
    }

    .nav-title {
        font-size: $font-size-navigation;
        line-height: $line-height-navigation;

        @include media-breakpoint-up( md ) {
            font-size: $font-size-navigation-md;
            line-height: $line-height-navigation-md;
        }
    }

}



// Menu toggle
.vp-menu-active.vp-menu-open {
    display: inline-block;
}

.vp-menu-active.vp-menu-close {
    display: block;
}

.vp-navigation.vp-menu-active {
    opacity: 1;
    visibility: visible;

}

.vp-navigation-bar {
    background: map-get($colors,white );
    box-shadow: 0px 0px 10px 0px map-get($colors,black );
    height: 52px;
    position: fixed;
    z-index: 1;
    width: 100%;
    top: 0;
    left: 0;
    transition: all .3s $easeOutExpo;

    &.hide {
        box-shadow: 0px 0px 10px 0px map-get($colors,black );
        position: fixed;
        max-width: 100%;
        left: 0;
        right: 0;
        top: 0;
        transform: translateY(-100%);
        transition: all .3s $easeOutExpo;
        transition-delay: .1s;
    }

    &.vp-menu-active {
        &.hide {
            transform: translateY(0);
        }
    }

    @include media-breakpoint-up( md ) {
        height: 80px;
    }

    .vp-menu-button {
        cursor: pointer;
        width: 48px;
        height: 48px;
        border: none;
        outline: none;
        background: none;
        padding: 10px;
        position: relative;
        overflow: hidden;
    }
    .vp-menu-open {
        opacity: 0;
        height: 5px;
        width: 5px;
        vertical-align: middle;
        background-color: map-get($colors, blue-lighter );
        border-radius: 50%;
        transition: all 2s $easeOutBack;
        &:nth-of-type(1) {
            transform: translateY(-30px);
        }
        &:nth-of-type(2) {
            transform: scale(4);
        }
        &:nth-of-type(3) {
            transform: translateY(30px);
        }
    }
    .vp-menu-close {
        position: absolute;
        opacity: 0;
        left: 0;
        top: 0;
        width: 48px;
        height: 48px;
        transition: all .4s $easeOutBack;
        &:after {
            position: absolute;
            left: 22px;
            top: 7px;
            content: '';
            height: 34px;
            width: 4px;
            border-radius: 5px;
            background-color: map-get($colors, blue-lighter );
            transition: all .2s linear;
        }
        &:before {
            position: absolute;
            left: 22px;
            top: 7px;
            content: '';
            height: 34px;
            width: 4px;
            border-radius: 5px;
            background-color: map-get($colors, blue-lighter );
            transition: all .2s linear;
        }
    }
    .vp-menu-active {
        &.vp-menu-open {
            opacity: 1;
            transition: all 1s $easeOutBack, opacity .3s linear;
            &:nth-of-type(1) {
                transform: translateY(0);
            }
            &:nth-of-type(2) {
                transform: scale(1);
            }
            &:nth-of-type(3) {
                transform: translateY(0);
            }
        }
        &.vp-menu-close {
            opacity: 1;
            transition: transform .7s $easeOutBack, opacity .1s linear;
            &:after {
                transform: rotate(-45deg);
            }
            &:before {
                transform: rotate(45deg);
            }
        }
    }

}

.vp-menu-button {
    &:hover {
        .vp-menu-close {
            transform: rotateZ(45deg);
            transition: transform .4s $easeInBack;
        }
    }
}
