/**
 * =============================================================================
 * Modules - Chapter 5
 * =============================================================================
 */

#vp-c5-s0 {
    overflow: hidden;
}

#svg-c5-s1 {
    width: 150%;
    margin-left: -25%;

    @include media-breakpoint-up(md){
        width: 100%;
        margin-left: auto;

    }
}

#svg-c5-s2 {
    width: 200%;
    margin-left: -50%;

    @include media-breakpoint-up(md){
        width: 100%;
        margin-left: auto;

    }
}

#svg-c5-s10 {
    width: 160%;
    margin-left: -40%;

    @include media-breakpoint-up(md){
        width: 100%;
        margin-left: auto;

    }
}

.vp-chap5-cta {
    outline: none;
    cursor: pointer;
    transition: box-shadow .3s $easeOutSine;
    border: 3px solid map-get($colors, white );
    color: map-get($colors, black-light );
    background: map-get($colors, white );
    box-shadow: 5px 5px 0 2px map-get($colors , blue-lightest );
    border-radius: 20px;
    font-weight: 500;
    font-size: 18px;
    padding: 14px 28px;

    @include media-breakpoint-up( md ) {
        font-size: 32px;
        padding: 18px 44px;
    }
    &:hover,
    &:active,
    &:visited,
    &:focus {
        outline: none;
    }
    &:hover {
        text-decoration: none;
        color: inherit;
        transition: box-shadow .3s $easeOutSine;
        box-shadow: 0 0 0 2px map-get($colors , blue-lightest );
    }
}

.vp-chap5-share {
    cursor: pointer;
    transition: all .3s;
    background: transparent;
    border: none;
    color: map-get($colors, white );
    font-size: 18px;
    font-weight: 500;

    @include media-breakpoint-up( md ) {
        font-size: 32px;
        width: 270px;
        height: 90px;
    }
    &:hover,
    &:active,
    &:visited,
    &:focus {
        outline: none;
    }
    &:hover {
        text-decoration: underline;
    }
}
.data-icons {
    svg {
        width: 300%;
        margin-left: -150%;

        @include media-breakpoint-up( md ) {
            width: 100%;
            margin-left: auto;
        }
    }
}

.means-icons {
    align-items: center;
    display: flex;
    flex-direction: column;

    svg {
        max-width: 180px ;
    }
}

#svg-c5-s9 {
    max-width: 160px;
}