.vp-hero-numbers {
    position: relative;
    margin: 0 auto;
    .vp-hero-number-inline {
        position: relative;
        display: inline-block;
        .vp-hero-number-value {
            display: inline-block;
            text-align: center;
            position: absolute;
            top:0;
            left:0;
            width: 100%;
            height: 100%;
        }
        .vp-hero-number-holder {
            visibility: hidden;
        }
    }
    span {
        font-size: inherit;
        // font-weight: inherit;
        margin: 0;
    }
}