        url('../fonts/flexo/Flexo-Regular-webfont.woff2') format('woff2'),
        url('../fonts/flexo/Flexo-Regular-webfont.woff') format('woff'),
        url('../fonts/flexo/Flexo-Regular-webfont.ttf') format('truetype'),
        url('../fonts/flexo/Flexo-Regular-webfont.svg#flexo') format('svg');
 }
