/**
 * =============================================================================
 * Modules - Home
 * =============================================================================
 */

#vp-home {
    .bg-home-desktop {
        visibility: hidden;
        opacity: 0;

        @include media-breakpoint-up(md){
            visibility: visible;
            opacity: 1;
        }
    }
    .bg-home-mobile {
        visibility: visible;
        opacity: 1;

        svg {
            top: 8%;
        }

        @include media-breakpoint-up(md){
            visibility: hidden;
            opacity: 0;
        }
    }
    .next-sheet {
        position: relative !important;
        width: auto !important;
    }

    overflow: hidden;
    max-height: 100vh;

    .h1 {
        opacity: 0;
    }
    .p {
        opacity: 0;
    }
    #svg-bg-home{
        g {
            opacity: 0;
        }
    }
    #svg-bg-home-mobile {
        path {
            opacity: 0;
        }
    }

    #svg-arrow-down {
        opacity: 0;
    }

    .vp-arrow {
        bottom: 20%;
        @media screen and (min-height: 800px ) {
            bottom: 15%;
        }
        @media screen and (max-width: 980px ) {
            bottom: 10%;
        }
    }

}