.swiper-button-prev, .swiper-button-next {
    background: $swiper-pagination-bg-color;
    color: $swiper-pagination-fill-color;
    height: 30px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    position: absolute;
    top: 50%;

    // .svg, svg{
    //     width: 30px;
    // }

    &.disabled {
        color: $secondary;
        &:hover{
            color: $secondary;
            cursor: not-allowed;
        }
    }

    &.swiper-button-disabled {
        opacity: 0;
        cursor: auto;
        pointer-events: none;
    }

    &:hover {
        color: $swiper-pagination-fill-color-hover;
        text-decoration: none;
    }

    [data-slide-class*="ui-white"] & {
        color: $white;
    }

    svg {
        transition: transform $swiper-timing-speed / 2 $easeOutCubic;
    }
}

