/**
 * =============================================================================
 * Components - Arrow classes
 * =============================================================================
 */

.vp-next-chapter {
    position: absolute;
    bottom: 10%;
    left: 0;
    right: 0;

}

.vp-arrow {

    svg {
        width:45px;
        height: 55px;
    }

    @include media-breakpoint-up( md ) {
        svg {
            width:65px;
            height: 75px;
        }
    }
}

.vp-arrow-white {
    svg {
        path {
            stroke: map-get($colors, white );
        }

    }

    a:hover {
        cursor: pointer;
        svg {
            path {
                fill: map-get($colors, white );
            }
        }
    }
}

.vp-arrow-white-reverse {
    transform: rotate(180deg);

    svg {
        path {
            stroke: map-get($colors, white );
        }
    }

    a:hover {
        cursor: pointer;
        svg {
            path {
                fill: map-get($colors, white );
            }
        }
    }
}

.vp-arrow-blue {
    svg {
        path {
            stroke: map-get($colors, blue );
        }
    }

    a:hover {
        cursor: pointer;

        svg {
            path {
                fill: map-get($colors, blue );
            }
        }
    }
}

.vp-arrow-green {
    svg {
        path {
            stroke: map-get($colors, green );
        }
    }

    a:hover {
        cursor: pointer;
        svg {
            path {
                fill: map-get($colors, green );
            }
        }
    }
}