@keyframes bounce {
	0%, 20%, 50%, 80%, 100% {
        transform:translateY(0);
  }
	40% {
        transform:translateY(-30px);
  }
	60% {
        transform:translateY(-15px);
  }
}

@keyframes bounceOld {
    0%, 20%, 60%, 100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(20px);
    }

    80% {
        transform: translateY(10px);
    }
}

@keyframes shake {
	0%, 20%, 50%, 80%, 100% {
        transform:translateY(0);
  }
	40% {
        transform:translateY(-30px);
  }
	60% {
        transform:translateY(-15px);
  }
}

