#vp-c3-s0 {
    overflow: hidden;
}
.vp-highlight-inaccurate {
    font-size: 45px;

    @media screen and (min-width:700px) {
        font-size: 70px;
    }

    @media screen and (min-width:999px) {
        font-size: 100px;
    }

    @media screen and (min-width:1250px) {
        font-size: 165px;
    }

}

#svg-c3-s4 {
    width: 100%;
    margin-left: -50%;
    margin-top: -60%;
    position: absolute;

    @include media-breakpoint-up(md){
        margin-left: auto;
        margin-top: auto;
        position: static;

    }
}

#svg-c3-s7 {
    width: 250%;
    margin-left: -75%;

    @include media-breakpoint-up(md){
        width: 100%;
        margin-left: auto;
    }
}