/**
 * =============================================================================
 * Components - Global classes
 * =============================================================================
 */

ul {
    list-style: none;
}

.vp-hide-x-flow {
    overflow-x: hidden;
}

.parallaxParent {
    height: 100%;
    overflow: hidden;
}
.parallaxParent > * {
    height: 200%;
    position: relative;
    top: -200%;
}
.parallaxParent-2 > * {
    height: 140%;
    position: relative;
    top: -140%;
}

section {
    position: relative;
}

.vp-fullscreen-h {
    min-height: 100%;
    height: 100%;

    @include media-breakpoint-up(md) {
        min-height: 100vh;
    }

    &.vp-header {
        height: 100vh;
    }

}


.vp-svg-bg {
    position: absolute;
    top:-40%;
    left:0;
    width: 100%;
    height: 100%;

    svg {
        position: relative;
        top:0;
        left:0;
        width: 100%;
        height: 100%;
    }
}

.vp-svg-bg-item {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;

    svg {
        position: absolute;
        top:10%;
        left:10%;
        width: 80%;
        height: 80%;
    }
}

.vp-svg-chapter-grid {
    position: absolute;
    bottom:0;
    left:0;
    width: 100%;
    height: 100%;

    svg {
        position: absolute;
        bottom:0;
        left:0;
        width: 100%;
        height: 60px;

        @include media-breakpoint-up( md ) {
            max-height: 100%;
        }
    }
}




@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    svg {
      height: 100%;
      width: 100%;
    }
  }


