#vp-c1-s0 {
    overflow: hidden;
}

#svg-bg-c1-1 {
    g {
        path {
            stroke: map-get($colors, blue-dark );
        }
        line {
            stroke: map-get($colors, blue-dark );
        }
        polygon {
            stroke: map-get($colors, blue-dark );
        }
    }
}

#svg-bg-c1-2 {
    g {
        path {
            stroke: map-get($colors, blue-dark );
        }
        line {
            stroke: map-get($colors, blue-dark );
        }
        polygon {
            stroke: map-get($colors, blue-dark );
        }
    }
}

#svg-c1-s4 {
    width:200%;
    margin-left: -50%;

    @include media-breakpoint-up(md){
        width:100%;
        margin-left: auto;
    }
}


#vp-c1-s1 {
    overflow: hidden;

    svg {
        width:120%;
        margin-left: -20%;

        @include media-breakpoint-up(md){
            width:100%;
            margin-left: -10%;
        }

        g {
            path {
            stroke:map-get($colors, grey );
            }
        }
    }
}

#svg-c1-s6 {
    width:200%;
    margin-left: -50%;

    @include media-breakpoint-up(md){
        width:100%;
        margin-left: auto;
    }
}

.vp-highlight-except {
    font-size: 70px;
    font-family: $font-family-base;

    @media screen and (min-width:700px) {
        font-size: 150px;
    }

    @media screen and (min-width:999px) {
        font-size: 200px;
    }

    @media screen and (min-width:1250px) {
        font-size: 270px;
    }

    div {
        font-size: inherit;
    }

}

.vp-highlight-except > span {
    font-size: 70px;
    font-family: $font-family-base;

    @media screen and (min-width:700px) {
        font-size: 150px;
    }

    @media screen and (min-width:999px) {
        font-size: 200px;
    }

    @media screen and (min-width:1250px) {
        font-size: 270px;
    }

    div {
        font-size: inherit;
    }
}
