#vp-social {
    position: relative;

    svg {
        fill: map-get( $colors, white ) !important;
    }

    &.data-state-closed {
        ul {
            opacity: 0;
            visibility: hidden;
            z-index: -1;
            bottom: 0;
            transition: all .6s $easeInOutQuad;
        }
    }

    &.data-state-open {


        ul {
            opacity: 1;
            visibility: visible;
        }
    }

    a {
        display: block;
        height: 100%;
        width: 100%;

        svg {
            display: block;
            height: calc( 100% - 2px );
            margin-left: 1px;
            margin-top: 1px;
            width: calc( 100% - 2px );
        }
    }

    li {
        display: inline-block;
        height: 100%;
        margin: 0 5px;
        opacity: 1;
        padding: 0;
        list-style: none;
        transform: none;
        transition: none;
        width: 40px;

        &:before {
            display: none;
        }
        &:hover {
            transform: scale(1.2);
        }

    }

    ul {
        margin: 0;
        opacity: 0;
        padding: 0;
        text-align: center;
        visibility: hidden;
        position: absolute;
        left: 0;
        right: 0;
        width: 100%;
        transition: all .6s $easeInOutQuad;

        @include media-breakpoint-up( md ) {
            bottom: -30%;
            left: 0%;
            right: 0;
        }
    }

}
