/**
 * =============================================================================
 * Global - Type
 * =============================================================================
 */
a {
    font-family: $font-family-base;
    font-weight: $font-weight-base;
}

button {
    font-family: $font-family-base;
    font-weight: $font-weight-base;
}

body {
    font-family: $font-family-base;
    font-size: $font-size-default;
    font-weight: $font-weight-base;
    line-height: $line-height-base;
}

h1, .h1 {
    font-family: $font-family-heading-h1;
    font-size: $font-size-h1;
    font-weight: $font-weight-h1;
    line-height: $line-height-h1;
    margin: $space-above-h1 0 $space-below-h1;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

h2, .h2 {
    font-family: $font-family-heading-h2;
    font-size: $font-size-h2;
    font-weight: $font-weight-h2;
    line-height: $line-height-h2;
    margin: $space-above-h2 0 $space-below-h2;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    strong {
        color: map-get( $colors, blue-dark );
    }
}

h3, .h3 {
    font-family: $font-family-heading-h3;
    font-size: $font-size-h3;
    font-weight: $font-weight-h3;
    line-height: $line-height-h3;
    margin: $space-above-h3 0 $space-below-h3;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

h4, .h4 {
    font-family: $font-family-heading-h4;
    font-size: $font-size-h4;
    font-weight: $font-weight-h4;
    line-height: $line-height-h4;
    margin: $space-above-h4 0 $space-below-h4;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

h5, .h5 {
    font-family: $font-family-heading-h5;
    font-size: $font-size-h5;
    font-weight: $font-weight-h5;
    line-height: $line-height-h5;
    margin: $space-above-h5 0 $space-below-h5;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

ol, ul {
    padding: 0;
    padding-left: $font-size-default;
    margin: $space-above-default 0 $space-below-default;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

p, .p {
    font-family: $font-family-base;
    font-size: $font-size-p;
    line-height: $line-height-p;
    margin: $space-above-default 0 $space-below-default;
    color: $black;
    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

small {
    font-size: $font-size-small;
    line-height: $line-height-small;
}

strong {
    font-family: $font-family-strong;
    font-weight: $font-weight-strong;
}

.smallprint {
    font-size: $font-size-smallprint;
    line-height: $line-height-smallprint;
}

.vp-text-white {
    h2, .h2 {
        strong {
            color: map-get( $colors, white );
        }
    }
}

.vp-text-bold {
    font-weight: $font-weight-strong;
}

@include media-breakpoint-up( md ) {
    body {
        font-size: $font-size-default-md;
        line-height: $line-height-base-md;
    }

    h1, .h1 {
        font-size: $font-size-h1-md;
        line-height: $line-height-h1-md;
        margin: $space-above-h1-md 0 $space-below-h1-md;
    }

    h2, .h2 {
        font-size: $font-size-h2-md;
        line-height: $line-height-h2-md;
        margin: $space-above-h2-md 0 $space-below-h2-md;
    }

    h3, .h3 {
        font-size: $font-size-h3-md;
        line-height: $line-height-h3-md;
        margin: $space-above-h3-md 0 $space-below-h3-md;
    }

    h4, .h4 {
        font-size: $font-size-h4-md;
        line-height: $line-height-h4-md;
        margin: $space-above-h4-md 0 $space-below-h4-md;
    }

    h5, .h5 {
        font-size: $font-size-h5-md;
        line-height: $line-height-h5-md;
        margin: $space-above-h5-md 0 $space-below-h5-md;
    }

    ol, ul {
        margin: $space-above-default-md 0 $space-below-default-md;
        padding-left: $font-size-default-md;
    }

    p, .p {
        font-size: $font-size-p-md;
        line-height: $line-height-p-md;
        margin: $space-above-default-md 0 $space-below-default-md;
    }

    small {
        font-size: $font-size-small-md;
        line-height: $line-height-small-md;
    }

    .smallprint {
        font-size: $font-size-smallprint-md;
        line-height: $line-height-smallprint-md;
    }
}