.swiper-ui-wrap{
    position: absolute;
    bottom: 34px;
    width: 100%;
    z-index: 999;
}


.slides-container {
	box-sizing: border-box;
	overflow: hidden;
	.swiper-slide {
		box-sizing: border-box;
		position: relative;
		text-align: left;
		min-height: 360px;
		padding-bottom: 110px; // cater for absolute ui wrap

		@include media-breakpoint-up(md) {
			min-height: 100vh;
			min-height: $slide-min-height;
			max-height: $slide-max-height;
		}
		.slide-container {
			z-index: 150;
			padding: 0;
			position: relative;

			@include media-breakpoint-up(sm) {
				min-height: $slide-min-height;
				max-height: $slide-max-height;
				overflow-y: scroll;
			}

			.slide-content {
				box-sizing: border-box;
				margin: auto;
				//-webkit-overflow-scrolling: touch;
				position: relative;
				vertical-align: middle;

				&.centered{
					text-align: center;
				}
			}
		}

	}
}

.swiper-container {
	//overflow: hidden;
	.swiper-wrapper {
		// don't specify a timing here, let it come from swiper's inline js
		transition-timing-function: $swiper-timing-curve;
		display: flex;
	}
}

.swiper{
	.slide, .swiper-slide {
		position: relative;
		&[data-visibility='hidden'] {
			display: none !important;
			visibility: hidden !important;
		}
	}
}

.swiper.slide-fade{
	.swiper-slide{
		.slide-container{
			opacity: 0;
			transition: opacity $swiper-timing-speed $swiper-timing-curve;
		}
		&.swiper-slide-active{
			.slide-container{
				opacity: 1;
			}
		}
	}
}

.slide-container {
	.swiper-slide-active & {
		.container {
			h3 {
				opacity: 1;
			}

			img,
			.rangeslider {
				opacity: 1;
			}

			.row:first-child {
				opacity: 1;
				transform: translateY( 0 );
			}
		}
	}

	img,
	.rangeslider {
		opacity: 0;
		transition: opacity $swiper-timing-speed linear $swiper-timing-speed / 1.5;
	}

	.container {
		> .row:first-child {
			opacity: 0;
			transform: translateY( -20px );
			transition: all $swiper-timing-speed $swiper-timing-curve $swiper-timing-speed / 4;
		}
	}

	.rangeslider {
		transition-delay: $swiper-timing-speed / 1.25;
	}

	div[class*=col] {
		&:nth-child( 2 ) {
			h3 {
				transition-delay: $swiper-timing-speed * 1.25;
			}
		}

		&:nth-child( 3 ) {
			h3 {
				transition-delay: $swiper-timing-speed * 1.5;
			}
		}

		h3 {
			opacity: 0;
			transition: opacity $swiper-timing-speed linear $swiper-timing-speed;
		}
	}
}
