/**
 * =============================================================================
 * Global - Colors
 * =============================================================================
 */

@for $i from 1 through length( $colors ) {
    $color-str: nth( map-keys( $colors ), $i );

    // Background colors
    .vp-bg-#{ $color-str } {
        background-color: map-get( $colors, $color-str );

        .vp-bg-color-inherit {
            background-color: map-get( $colors, $color-str );
        }
    }

    // SVG fill colors
    .vp-svg-fill-#{ $color-str } {
        fill: map-get( $colors, $color-str );
    }

    // SVG outline colors
    .vp-svg-outline-#{ $color-str } {
        fill: none;
        stroke: map-get( $colors, $color-str );
    }

    // Text colors
    .vp-text-#{ $color-str } {
        color: map-get( $colors, $color-str );
    }

    // Text colors
    .vp-bordered-#{ $color-str } {
        border-left: 8px solid map-get( $colors, $color-str );
        border-right: 8px solid map-get( $colors, $color-str );

        @media screen and (min-width:770px) {
            border-left: 20px solid map-get( $colors, $color-str );
            border-right: 20px solid map-get( $colors, $color-str );
        }
    }
}